.Information {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.2);
  z-index: 99;
  text-align: center;
  &::after {
    content: "";
    display: inline-block;
    height: 100%;
    vertical-align: middle;
  }
  .Information-iner {
    display: inline-block;
    width: 500px;
    background: white;
    box-shadow: 0px 4px 12px 0px rgba(0, 0, 0, 0.2);
    vertical-align: middle;
    .Information-head {
      display: flex;
      justify-content: space-between;
      padding: 15px 24px;
      border-bottom: 1px solid #e9e9e9;
      span,
      i {
        height: 24px;
        font-size: 18px;
        //   
        color: black;
        line-height: 24px;
      }
      i {
        cursor: pointer;
      }
    }
    .Information-body{
      text-align: left;
      padding: 15px 24px;
      .Information-list{
        height: 75px;
        &::after {
          content: "";
          display: inline-block;
          height: 100%;
          vertical-align: middle;
        }
        .p-d-i-icon {
          display: inline-block;
          font-size: 32px;
          vertical-align: middle;
          margin-right: 10px;
          i {
            font-size: 32px;
          }
        }
        .num-text {
          display: inline-block;
          padding: 0 3px;
          vertical-align: middle;
          .text {
            margin: 0;
            height: 24px;
            font-size: 13px;
            //    
            font-weight: 400;
            color: rgba(1, 1, 1, 0.6);
            line-height: 24px;
          }
          .num {
            margin: 0;
            height: 24px;
            font-size: 16px;
            //    
            font-weight: 400;
            color: rgba(25, 25, 25, 1);
            line-height: 24px;
          }
        }
      }
    }
  }
}
